/*! tailwindcss v4.1.17 | MIT License | https://tailwindcss.com */
.fixed {
  position: fixed;
}
.mx-auto {
  margin-inline: auto;
}
.min-h-screen {
  min-height: 100vh;
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
}
@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
}
